@charset "UTF-8";

.SiteWrap {
  width: 100%;
  margin: 0 auto;
  display: flex;

  aside {
    width: 166px;
    padding:  5px 10px 0;
    ul {
      list-style: none;
    }
  }

  @mixin maru {
    width: 100px;
    height: 100px;
  }

  .ContentsWrap  {
    width: 100%;
    margin-left: 20px;
    .HeadBox {
      width: 100%;
      height: 80px;
    }
  } //ContentsWrap end
}
